import React,{Component} from 'react'
import { Carousel,Input} from 'antd';
import {HashRouter as Router, NavLink, Redirect, Route, Switch} from "react-router-dom";
import './Main.css';
import $ from 'jquery'
//import imgurl from '.././footer.png';

const Search = Input.Search;
class Main extends Component{
    constructor(props){
        super(props)
        this.state={
            list:[],
            pic:[],
            pic02:[],
            pic03:[],
            arrL:[]
        }
    }
    change(e){
        $.ajax({
            url:'https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su?wd='+e.target.value,
            dataType:'jsonp',
            /*回调函数名称*/
            jsonp:'cb',
            success:(data)=>{
                console.log(data)
                let len = data.s.length;
                let temp = [];
                for(var i=0;i<len;i++){
                    temp.push(<li key={i}>{data.s[i]}</li>);
                }
                this.setState({
                    arrL:temp
                });
            }
        })
    }
    render() {

        return(
            <div>
                <div className='container'></div>
                <div className='banner'>
                <Carousel autoplay>
                    {
                        this.state.list.map(function (item,index) {
                            return(
                                <div key={index} className='pic'>
                                   <div><img src={item.banner_img_url} alt=""/></div>
                                </div>
                            )

                        })
                    }

                </Carousel>
                    <div className='ipt'>
                        <Search placeholder="请输入目的地或者产品名称，例如:香港" onChange={this.change.bind(this)} enterButton style={{ width: 700}} size='large' autosize='true'/>
                        <ul>{this.state.arrL}</ul>
                    </div>

                </div>
                <div className='citypicture'>
                    <div className='citypicturetitle'>
                        <h1>探索世界</h1>
                        <p>用心去感受世界的每个角落都如此精彩</p>
                    </div>
                    <div className='pictures'>
                        {
                            this.state.pic.map(function (item,index) {
                                return(
                                    <NavLink key={index} to={{pathname:'/home/detail',state:{id:item.id}}}><span className='pic01'>
                                        <img src={item.img}/>
                                    </span></NavLink>
                                )

                            })
                        }
                    </div>

                </div>
                <div className='person'>
                    <div className='personin'>
                    <div className='person01'>
                        <h1>花花世界</h1>
                        <p>世界的精彩在于你看待世界的角度</p>
                    </div>
                    <div className='person02'>
                        <div className='person03'>
                            {
                                this.state.pic02.map(function (item,index) {
                                    return(
                                        <span key={index} className='pic02'>
                                        <img src={item.img} alt=""/>
                                    </span>
                                    )

                                })
                            }
                        </div>
                    </div>
                    </div>
                </div>
                <div className='feel'>
                    <div className='feel01'>
                        <h1>环球体验家</h1>
                        <p>借助你的眼睛，让后来者看到世界的精彩</p>
                    </div>
                    <div className='feel02'>
                        {
                            this.state.pic03.map(function (item,index) {
                                return(
                                    <div key={index} className='pic03'>
                                        <dl><img src={item.img} alt=""/></dl>
                                        <dt>{item.title}</dt>
                                        <br/>
                                        <p>{item.detail}</p>

                                    </div>
                                )

                            })
                        }

                    </div>
                </div>
                <div className='foot'>
                   <div className='foot01'>
                        <h1><i><span className="icon iconfont">&#xe60f;</span></i><span>400-825-3686</span></h1>
                       <p>周一至周日：08:00-23:00 节假日：08:00-20:00</p>
                       <p><span className="icon iconfont">&#xe608;</span>
                           <span className="icon iconfont">&#xe679;</span></p>
                   </div>
                   <div className='foot02'>
                       <ul>
                           <li><a href="">关于出国去</a></li>
                           <li><a href="">关于出国去</a></li>
                           <li><a href="">关于出国去</a></li>
                           <li><a href="">关于出国去</a></li>
                           <li><a href="">关于出国去</a></li>
                           <li><a href="">关于出国去</a></li>
                           <li><a href="">关于出国去</a></li>
                           <li><a href="">关于出国去</a></li>
                           <li><a href="">关于出国去</a></li>
                       </ul>
                   </div>
                </div>

            </div>
        )
    }
    componentDidMount(){
        var _this=this;
        $.ajax({
            type:"get",
            url:" http://jx.xuzhixiang.top/ap/api/bannerlist.php",
            data:{uid:1932},
            async:true,
            dataType:"json",
            success:function(data){
                //console.log(data.data)
               _this.setState({list:data.data})
            }
        });


        $.ajax({
            type:"get",
            url:" http://localhost:2000/users/pic01",
            async:true,
            dataType:"json",
            success:function(data){
                console.log(data[0].id)
                _this.setState({pic:data})
            }
        });



        $.ajax({
            type:"get",
            url:" http://localhost:2000/users/pic02",
            async:true,
            dataType:"json",
            success:function(data){
               // console.log(data)
                _this.setState({pic02:data})
            }
        });



        $.ajax({
            type:"get",
            url:" http://localhost:2000/users/pic03",
            async:true,
            dataType:"json",
            success:function(data){
                //console.log(data)
                _this.setState({pic03:data})
            }
        });

    }

}
export default Main