import React,{Component} from 'react'
import './Hotel.css'
import { Input } from 'antd';
import axios from 'axios'
import $ from "jquery"

const Search = Input.Search;
class Hotel extends Component{
    constructor(){
        super()
        this.state={
            img1:'http://ac-r.static.booking.cn/static/img/china/cny-campaign/index-banner3/03500260ffb038ec4b5701c3e127fe56bdbfc0c4.jpg',
            list:[],
            arrL:[]
        }
    }

    change(e){
        $.ajax({
            url:'https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su?wd='+e.target.value,
            dataType:'jsonp',
            /*回调函数名称*/
            jsonp:'cb',
            success:(data)=>{
                console.log(data)
                let len = data.s.length;
                let temp = [];
                for(var i=0;i<len;i++){
                    temp.push(<li key={i}>{data.s[i]}</li>);
                }
                this.setState({
                    arrL:temp
                });
            }
        })
    }
    render() {

        return(
            <div>
                <div className='container'></div>
                <div className='titl'>
                    <h1>缤纷四季优惠搜不停</h1>
                    <p>从舒适的乡村民宿到时髦的都市公寓
                    </p>
                </div>
                <div className='search'>
                    <Search
                        placeholder="目的地"
                        enterButton="搜索"
                        size="large"
                        onChange={this.change.bind(this)}
                    />
                    <ul>{this.state.arrL}</ul>
                </div>
                <div className='bigpic'>
                    <img src={this.state.img1} alt=""/>

                </div>
                <div className='smallpictitle'>
                    <p>更多独特住宿，让假期充满格调</p>
                </div>

                <div className='smallpic'>
                    {
                       this.state.list.map(function (item,index) {
                           return(
                               <div>
                                   <span>

                                       <div><img src={item.pimg} alt=""/></div>
                                   <div className='pdesc'>
                                       <h4>{item.pname}</h4>
                                       <h6>{item.pdesc}</h6>

                                   </div>

                                   </span>
                               </div>
                           )


                       })

                    }
                </div>


               <div className='other'>



               </div>

            </div>
        )
    }
    componentDidMount() {
        var _this=this
            axios({
                method:'get',
                url:' http://jx.xuzhixiang.top/ap/api/productlist.php',
                params:{uid:1932},
            }).then(function (data) {
                console.log(data.data.data)

                _this.setState({
                    list:data.data.data
                })
            })
    }

}
export default Hotel