import React,{Component} from 'react'
import {HashRouter as Router, NavLink, Redirect, Route, Switch} from "react-router-dom";
import { Modal, Button } from 'antd';
import './home.css';
import Hotel from './Hotel'
import Cart from './Cart'
import Main from './Main'
import Register from  './Register'
import { Input, Tooltip, Icon } from 'antd';
import { message} from 'antd';
import Detail from './Detail'

class Home extends Component{
    constructor(props){
        super(props)
        this.state={
            icon:'http://www.chuguoqu.com/images/header-logo.png',
            username:'',
            password:''
        }
    }
    state = {
        loading: false,
        visible: false,
    };
    showModal = () => {
        this.setState({
            visible: true,
        });
    };
    handleCancel = () => {
        this.setState({ visible: false });
    };
    handleOk = () => {

        console.log(this.state.username)
        console.log(this.state.password)
        message.success('登录成功');


        this.setState({ loading: true });
        setTimeout(() => {
            this.setState({ loading: false, visible: false });
        }, 3000);
    };
    tag1(event){
        this.setState({
            username:event.target.value
        })
    }
    tag2(event){
        this.setState({
            password:event.target.value
        })
    }

    render() {
        const { visible, loading } = this.state
        return(
            <div>
                <Router><div>
               <div className='top'>
                   <div className='toplist'>
                       <div className='list_left'>
                           <img src={this.state.icon} alt=""/>
                       </div>
                       <div className='list_center'>

                       <li><NavLink to='/home/main'>首页</NavLink></li>
                       <li><div className='place'>
                           <div className='placetop'></div>
                           <div>
                           address
                           </div>
                       </div><NavLink to='#'>目的地</NavLink></li>
                       <li><NavLink to='/home/hotel'>酒店</NavLink></li>
                       <li><NavLink to='/home/cart'>购物车</NavLink></li>
                       <li><NavLink to='/downloadapp'>App下载</NavLink></li>
                       </div>
                       <div className='list_right'>
                           <span onClick={this.showModal}>登陆</span>|
                           <span><NavLink to='/home/register'>注册</NavLink></span>
                       </div>








                       <Modal
                           style={{width:600}}
                           visible={visible}
                           title="登录账号"
                           onOk={this.handleOk}
                           onCancel={this.handleCancel}
                           footer={[
                               <Button  key="submit" type="primary" loading={loading} onClick={this.handleOk.bind(this)}>
                                   登录
                               </Button>,
                               <Button key="back" className='loginbtn'>
                                   忘记密码
                               </Button>,
                           ]}
                       >
                           <div className='login'>

                               <Input
                                   onChange={this.tag1.bind(this)}
                                   className='username'
                                   placeholder="请输入用户名"
                                   prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                   suffix={
                                       <Tooltip title="🙂">
                                           <Icon type="info-circle" style={{ color: 'rgba(0,0,0,.45)' }} />
                                       </Tooltip>
                                   }
                               />

                               <div className='empty'></div>

                               <Input.Password onChange={this.tag2.bind(this)} placeholder="请输入密码" prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />}/>
                           </div>

                       </Modal>





                   </div>

               </div>

                    <Switch>
                        <Route path='/home/main' component={Main}></Route>
                        <Route path='/home/hotel' component={Hotel}></Route>
                        <Route path='/home/cart' component={Cart}></Route>
                        <Route path='/home/register' component={Register}></Route>
                        <Route path='/home/detail' component={Detail}></Route>



                    </Switch>
                </div></Router>
            </div>
        )
    }

}
export default Home