import React,{Component} from 'react'
import $ from "jquery";
import  './Detail.css'
import { Button } from 'antd';
import { DatePicker } from 'antd';
import moment from 'moment';
import { InputNumber } from 'antd';

const dateFormat = 'YYYY/MM/DD';
class Detail extends Component{
    constructor(props){
        super(props);
        this.state={
            list:{},
            id:'',
            datetime:'',
            personNumber:''
        }
    }
    day(value) {
        //console.log(value._d);
        this.setState({datetime:value._d})
    }

     num(value) {
        console.log(value);
        this.setState({personNumber:value})
    }
    commit(){
        if(localStorage.getItem('cart')){
            var obj=JSON.parse(localStorage.getItem('cart'));
            var id=this.state.id;
            obj[id]=this.state.personNumber;
            console.log(obj)
            localStorage.setItem('cart',JSON.stringify(obj))
        }else {
            var obj={};
            localStorage.setItem('cart',JSON.stringify(obj))
        }

        this.props.history.push('/home/cart')
    }
    render() {

        return(
            <div>
                <div className='container'></div>
                <div className='detail'>

                <div className='cart'>
                    <h1>Lorem ipsumelit. Dolorem, iusto? Aliquam!</h1>
                    <img src={this.state.list.img} alt=""/>
                </div>

                    <div className='cart_right'>
                <p><span>售价:</span><span>￥28</span></p>
                <p><span>当地价:</span><s>￥100</s></p>
                <p><span>出游日期:</span>
                    <DatePicker defaultValue={moment('2019/01/01', dateFormat)} format={dateFormat} onChange={this.day.bind(this)} /></p>
                <p><span>数量选择:</span><InputNumber min={1} max={1} defaultValue={99} ref='txt' onChange={this.num.bind(this)} /></p>
                <p><Button onClick={this.commit.bind(this)} size='large' type="primary">加入购物车</Button><Button size='large' type="primary">加入收藏</Button></p>
                <p>下载APP购买更便宜</p>
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount() {
        var _this=this

        if(this.props.location.state!==undefined){
            this.setState({id:this.props.location.state.id})
            localStorage.setItem('id',this.props.location.state.id)
        }else if(this.props.location.state==undefined){
            this.setState({id: localStorage.getItem('id')})
        }
        //console.log(this.props.location.state.id)
       /* var id=this.props.location.state.id
        localStorage.setItem('id',id)
        if(id!==undefined){
            this.setState({id:id})
        }else {
           var id=localStorage.getItem('id')
            console.log(id)
            this.setState({id:id})
        }
*/
        $.ajax({
            type:"get",
            url:" http://localhost:2000/users/pic01",
            async:true,
            dataType:"json",
            success:function(data){

                data.map(function (item,index) {
                    if(index==_this.state.id){
                        _this.setState({list:data[index]})
                    }
                })
            }
        });
    }
}
export default Detail