import React,{Component} from 'react'
import $ from 'jquery'
import './Cart.css'
import { InputNumber } from 'antd';

class Cart extends Component{
    constructor(props){
        super(props);
        this.state={
            arr:[],
            num:'',
            totalPrice:''
        }
    }
     onChange(e,index) {

        console.log(e,index)
         var _this=this

        this.state.arr.map(function (item,i) {
             //console.log(i)
             if(i==index){
                 console.log(i)
                 item.num=e
                 console.log(item)
                 //_this.state.arr[i].num=e 
                 //console.log(item)  
             }
         })
        this.setState({arr:this.state.arr})
         //console.log(this.state.arr)
         
         /*this.setState({
             arr: arr
         })*/
        
        
        
    }


    del(e,index){
   //console.log(e)
       // console.log(index)
        //console.log(this.state.arr[index].id)
        var id1=this.state.arr[index].id
       this.state.arr.splice(index,1)
        //console.log(this.state.arr)
        this.setState({arr:this.state.arr});
        var obj=JSON.parse(localStorage.getItem('cart'))
        //console.log(obj)
        for(let i in obj){
            //console.log(i)
            if(id1==i){
                //console.log(i)
                //console.log(toString(i))
                var k=''+i
                delete obj[k];
                //console.log(obj)
                localStorage.setItem('cart',JSON.stringify(obj))
            }
        }
    }
    //获取单选框的值
    getCheckedChange=(e,i)=>{
        console.log(e.target.checked,i)
//文本框的值 e.target.value 需要赋值给 json 数据的下标为index

        this.setState({
            arr:this.state.arr.map((ele,index)=>{
                if(index==i){
                    ele.checked=e.target.checked
                    return ele
                }else {
                    return ele
                }
            })
        })


        this.SumPrice()
    }

    //点击全选和全不选
    CheckedChange=(e)=> {
        //console.log(e.target.checked)
        if (e.target.checked == true) {
            this.setState({
                arr: this.state.arr.map((ele, index) => {
                    ele.checked = true
                    return ele
                })
            })

        } else if (e.target.checked == false) {
            this.setState({
                arr: this.state.arr.map((ele, index) => {
                    ele.checked = false
                    return ele
                })
            })
        }
       /* var arr2=[];
        this.state.arr.map(function (item,index) {
            if(item.checked==true){
                arr2.push(item)
            }
            if(arr2.length==this.state.arr.length){
                e.target.checked =true
            }else {
                e.target.checked =false
            }

        })*/

        this.SumPrice()
    }

    SumPrice=()=> {
        var sum = 0
        this.state.arr.forEach((ele, index) => {
            sum += ele.num * ele.price
        })
        this.setState({
            totalPrice: sum
        })
    }
    render() {
        //console.log(this.state.arr)
        var _this=this
        return(
            <div>
                <div className='container'></div>
                <div className='to'>222</div>
                <div className='cart1'>
                    <span>全选<input type="checkbox" onChange={
                        (e)=>{
                            this.CheckedChange(e)
                        }
                    }/></span>
                    <span>产品信息</span>
                    <span>单价</span>
                    <span>数量</span>
                    <span>总价</span>
                    <span>操作</span>
                </div>
                <div className='cart2'>
                    {
                        this.state.arr.map(function (item,index) {
                            return(
                                <div key={index} className='cartlist'>

                                    <div>

                                        <input type="checkbox" checked={item.checked} onChange={
           (e)=>{
    _this.getCheckedChange(e,index)
    }
    }/>
                                        <img src={item.img} alt=""/>
                                    </div>

                                    <span>{item.type}</span>
                                    <span>￥{item.price}</span>
                                    <div>
                                                            <InputNumber min={1} max={1000}  onChange={(e)=>{_this.onChange(e,index)}}
                                                                                                          defaultValue={1}  />

                                                                    </div>

                                    <span>￥{item.price*item.num}</span>
<button onClick={(e)=>{
     //console.log(e,index)
    _this.del(e,index)
}}>删除</button>

                                </div>
                            )
                        })
                    }
                </div>
                <div className='total'>
                    <p>总价:￥{this.state.totalPrice}</p>
                </div>

            </div>
        )
    }
    componentDidMount() {
        var _this=this;
        $.ajax({
            type:'get',
            url:'http://localhost:2000/users/pic01',
            dataType:'json',
            async:'true',
            success:function (data){
                //console.log(data)
                var arr2=[]
                data.map(function (item,index) {
                    var obj=JSON.parse(localStorage.getItem('cart'))
                     for(let i in obj){
                         //console.log(i);
                         console.log(obj[i])
                         if(i==index){
                             //console.log(item)
                             arr2.push(item)

                             
                         }
                         //console.log(arr2)
                     }
                })
               // console.log(Array.isArray(arr2))
               // console.log(...arr2)

                _this.setState({arr:[...arr2]})
            }
        })
    }

}
export default Cart