import React,{Component} from 'react'
import {HashRouter as Router, NavLink, Route} from "react-router-dom";
import './downloadapp.css';
import { Modal, Button } from 'antd';


class Downloadapp extends Component{
    constructor(){
        super()
        this.state={
            img_id:'http://www.chuguoqu.com/images/app2-1/mountain.png',
            img_logo:'http://www.chuguoqu.com/images/app2-1/logo.png',
            img_moon:'http://www.chuguoqu.com/images/app2-1/moon.png',
            img_ewm:'http://www.chuguoqu.com/images/app2-1/QR.png'
        }
    }
    state = { visible: false };

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    hideModal = () => {
        this.setState({
            visible: false,
        });
    };

    render() {

        return(
            <div>
            <div  className='bodystyle'>
                <img className='img01' src={this.state.img_id} alt=""/>
                <img className='img02' src={this.state.img_moon}  alt=""/>
                <img className='img03' src={this.state.img_logo}  alt=""/>
                <button className='btn01' onClick={this.showModal}>立即下载</button>
                <button className='btn02'>功能介绍</button>
            </div>
                <Modal
                    visible={this.state.visible}
                    onOk={this.hideModal}
                    onCancel={this.hideModal}
                    okText="知道了"
                    cancelText="谢谢"
                >
                    <img src={this.state.img_ewm} alt=""/>

                </Modal>

            </div>
        )
    }

}
export default Downloadapp