import React,{Component} from 'react'
import './register.css'
import {Icon, Input, Tooltip} from "antd";
import { message, Button,Modal } from 'antd';

class Register extends Component{


   success = () => {
        message.success('注册成功');
    };
    state = { visible: false };

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    hideModal = () => {
        this.setState({
            visible: false,
        });
    };

    render() {
        return(
            <div>
                <div className='container'></div>
                <div>

                    <div className='title'>
                        <h1>30秒快速注册出国去账号</h1>
                    </div>
                    <div className='register'>
                        <div className='res01'>
                            <div className='username'>
                                <span>*账号</span>
<Input size='large' placeholder="请输入账号" allowClear onChange={this.tag} />
                            </div>

                            <div className='password'>
                                <span>*密码</span>
                                <Input size='large' placeholder="请输入密码" allowClear onChange={this.tag} />
                            </div>

                            <div className='nicheng'>
                                <span>*昵称</span>
                                <Input size='large' placeholder="请输入昵称" allowClear onChange={this.tag} />
                            </div>

                            <div className='yzm'>
                                <span>*验证码</span>
                                <Input size='large' placeholder="请输入验证码" allowClear onChange={this.tag} />
                            </div>

                            <div className='btn'>
                                <Button type="danger" onClick={this.success}>立即注册</Button>
                                <input type="checkbox"/>
                            <span><a onClick={this.showModal}>我已阅读并同意出国去协议</a></span>
                            </div>
                            <Modal
                                visible={this.state.visible}
                                onOk={this.hideModal}
                                onCancel={this.hideModal}
                                okText="知道了"
                                cancelText="谢谢"
                            >
                                <p><h1>《出国去用户协议》</h1>

                                    <p>一、接受条款</p>

                                    出国去根据以下服务条款为您提供服务。这些条款可由出国去随时更新，且毋须另行通知。出国去用户协议（以下简称"用户协议"）一旦发生变动，出国去将在网页上公布修改内容。修改后的用户协议一旦在网页上公布即有效代替原来的用户协议。 您在使用出国去提供的各项服务之前，应仔细阅读本用户协议。如您不同意本用户协议及/或随时对其的修改，请您立即停止使用出国去所提供的全部服务；您一旦使用出国去服务，即视为您已了解并完全同意本用户协议各项内容，包括出国去对用户协议随时所做的任何修改，并成为出国去用户（以下简称"用户"）。</p>

                            </Modal>
                        </div>



                        <div className='res02'>
                            <h1>已有出国去账号？<a href="#">点击登录</a></h1>
                            <h1>快速登录</h1>
                            <h1 className='icon'><span className="icon iconfont">&#xe608;</span>
                                <span className="icon iconfont">&#xe679;</span></h1>

                        </div>
                    </div>
                </div>

            </div>
        )
    }

}
export default Register